<template>
  <div class="col-sm-9 col-md-10 col-xs-offset-2 col-md-offset-2 col-sm-offset-3 Blogging">
    <form v-show="!submmited">
      <div class="form-group">
        <label for="title">博客标题</label>
        <input type="text" class="form-control" id="title" placeholder="请输入标题" v-model="blog.title">
      </div>
      <div class="form-group">
        <label for="author">博客作者</label>
        <input type="text" class="form-control" id="author" placeholder="请输入作者" v-model="blog.author">
      </div>
      <div class="form-group">
        <label for="context">博客内容</label>
        <textarea class="form-control" rows="5" id="context" placeholder="请输入内容"  v-model="blog.context"></textarea>
      </div>
      <div class="form-group">
        <label for="cation">博客分类</label>
          <select class="form-control" id="cation"  v-model="blog.cation">
            <option value="生活类">生活类</option>
            <option value="技术类">技术类</option>
            <option value="娱乐类">娱乐类</option>
            <option value="音乐类">音乐类</option>
            <option value="影视类">影视类</option>
          </select>
      </div>
      <button type="submit" class="btn btn-success btn-default btn-sm btn-lg btn-block" @click.prevent="post()">提交</button>
    </form>

    <div class="text-center" v-if="submmited">
      <img src="../assets/ok.gif" alt="" class="img-responsive">
      <h2>博客已上传成功</h2>
      <router-link to="/" tag="button" class="btn btn-info">前去查看</router-link>
      <button type="button" class="btn btn-info" @click.prevent="argin()">再写一篇</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Blogging',
  data(){
    return{
      blog:{
          title:'',
          context:'',
          author:'',
          cation:'生活类'
      },
      submmited:false
    }
  },
  methods:{
    post:function(){
      this.$http.post("https://jsonplaceholder.typicode.com/posts",{
        title:this.blog.title,
        body:this.blog.context,
        userId:1
      })
      .then(res=>{
        this.submmited = true;
        console.log(res);
      })
    },
    argin:function(){
      this.submmited = false;
    }
    
  }
}
</script>

<style scoped>
.Blogging{
  height: 100%;
background: #fff;
/*padding: 10px 0px;*/
}
form{
  padding-top: 20px;
}
img{
  margin: 0 auto;
}
button{
  margin: 5px;
}
</style>