import ShowBlogs from './components/blogBrowse.vue'
import Blogging from './components/Blogging.vue'
import SingleBlog from './components/SingleBlog.vue'
import CompileBlog from './components/CompileBlog.vue'
export default[
	{path:'/',component:ShowBlogs},
	{path:'/add',component:Blogging},
	{path:'/blog/:id',component:SingleBlog},
	{path:'/edit/:id',component:CompileBlog}
]