import Vue from 'vue'
import VueResource from 'vue-resource'
import App from './App.vue'
import $ from 'jquery'
import VueRouter from 'vue-router'
import Routes from './router'

Vue.config.productionTip = false

Vue.use(VueResource)
Vue.use(VueRouter)

const router = new VueRouter({
	routes:Routes,
	mode:'history'
})

new Vue({
  render: h => h(App),
  router:router
}).$mount('#app')
